.btn
	display: inline-block
	width: 360px
	border: 2px solid #c7b299
	color: #000
	font-size: 15px
	letter-spacing: 0.1em
	line-height: 62px
	text-transform: uppercase
	margin: 0 auto
	border-radius: 62px
	background-color: #FFF
	outline: none
	max-width: 100%
	+tr(.4s)
	+rm
	+r(650)
		width: 85%
		height: 50px
		line-height: 50px
		font-size: 12px
	&:hover
		background-color: #c7b299
		color: #fff
	&.mod_offset
		margin: 45px 0 60px
		background-color: $n
		&:hover
			background-color: #c7b299
		+r(650)
			display: block
			width: auto
			margin: 35px 20px 10px
	&.mod_reverse
		background-color: #c7b299
		color: #fff
		&:hover
			background-color: #FFF
			color: #000
	&.mod_change_650
		+r(650)
			background-color: #c7b299
			color: #fff
			&:hover
				background-color: #FFF
				color: #000
	&__article
		height: 46px
		width: 250px
		text-align: center
		line-height: 43px
		margin-top: 5.5rem
		font-size: 12px

	&__open.is_active
		span
			background: #fff
			&:nth-child(1)
				-webkit-transform: rotate(45deg)
				-moz-transform: rotate(45deg)
				-o-transform: rotate(45deg)
				transform: rotate(45deg)
				width: 126%
			&:nth-child(2)
				width: 0%
				opacity: 0
			&:nth-child(3)
				-webkit-transform: rotate(-45deg)
				-moz-transform: rotate(-45deg)
				-o-transform: rotate(-45deg)
				transform: rotate(-45deg)
				width: 126%
	&__open,
	&__close
		width: 62px
		height: 62px
		position: absolute
		top: 0
		right: 3rem
		background-color: $n
		border: none
		outline: none
		&:before,
		&:after
			content: none
	&__open
		span
			display: block
			position: absolute
			height: 2px
			width: 100%
			background: $fiolet
			border-radius: 2px
			opacity: 1
			left: 0
			-webkit-transform: rotate(0deg)
			-moz-transform: rotate(0deg)
			-o-transform: rotate(0deg)
			transform: rotate(0deg)
			-webkit-transition: .25s ease-in-out
			-moz-transition: .25s ease-in-out
			-o-transition: .25s ease-in-out
			transition: .25s ease-in-out
			&:nth-child(1)
				top: 1px
				-webkit-transform-origin: left center
				-moz-transform-origin: left center
				-o-transform-origin: left center
				transform-origin: left center
			&:nth-child(2)
				top: 50%
				-webkit-transform-origin: left center
				-moz-transform-origin: left center
				-o-transform-origin: left center
				transform-origin: left center
				margin-top: 0px
			&:nth-child(3)
				top: 100%
				-webkit-transform-origin: left center
				-moz-transform-origin: left center
				-o-transform-origin: left center
				transform-origin: left center
				margin-top: -2px
	&__close
		span
			display: block
			position: absolute
			height: 4px
			width: 100%
			background: $fiolet
			border-radius: 4px
			opacity: 1
			left: 0
			-webkit-transform: rotate(0deg)
			-moz-transform: rotate(0deg)
			-o-transform: rotate(0deg)
			transform: rotate(0deg)
			-webkit-transition: .25s ease-in-out
			-moz-transition: .25s ease-in-out
			-o-transition: .25s ease-in-out
			transition: .25s ease-in-out
			&:nth-child(1)
				-webkit-transform: rotate(45deg)
				-moz-transform: rotate(45deg)
				-o-transform: rotate(45deg)
				transform: rotate(45deg)
				width: 130%
			&:nth-child(2)
				width: 0%
				opacity: 0
			&:nth-child(3)
				-webkit-transform: rotate(-45deg)
				-moz-transform: rotate(-45deg)
				-o-transform: rotate(-45deg)
				transform: rotate(-45deg)
				width: 130%

	&__hide
		display: none
		height: 45px
		line-height: 43px
		border: 2px solid #c7b299
		background-color: #c7b299
		color: #fff
		text-transform: uppercase
		font-size: 12px
		letter-spacing: .1em
		text-align: center
		margin: 40px auto 0
		+r(650)
			display: block

	&__spec
		display: inline-block
		width: auto
		padding: 0 20px
		display: inline-block
		border: 2px solid #c7b299
		color: #000
		font-size: 15px
		letter-spacing: 0.1em
		line-height: 62px
		text-transform: uppercase
		margin: 0 auto
		border-radius: 62px
		outline: none
		max-width: 100%
		+tr(.4s)
		+rm
		+r(1000)
			height: 34px
			line-height: 30px
			padding: 0 20px
		+r(850)
			font-size: 12px
		+r(400)
			font-size: 10px
			padding: 0 10px
		&:hover
			background-color: #c7b299
			color: #fff